<template>
  <div class="sv-form-wrapper  h-full" v-if="ready">
    <div :class="{'compact': compact}" class="sv-form bg-white">
      <!--      <sv-boolean-field v-model="compact"></sv-boolean-field>-->
      <div class="card-header" v-if="title"><h2>{{ title }}</h2></div>

      <sv-form-fields
          :fields="fields"
          :form="form"
      ></sv-form-fields>

      <slot :form="form" name="fields"></slot>

      <div class="card-footer">

        <div class="form--actions flex items-center justify-end" v-if="!form.parent">

          <div class="form--sub-actions flex flex-row-reverse">
            <div class="ml-2" v-for="(button, key) in form.subActions">
              <sv-button :button="button" :style="{ opacity: 1 - ((key + 1) * .3)}"></sv-button>
            </div>
          </div>

          <div class="ml-8">
            <sv-button :button="form.defaultAction"></sv-button>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvButton from '../SvButton'
import FormModel from './FormModel'
import SvFormFields from './SvFormFields'
import SvBooleanField from './fields/SvBooleanField'

export default {
  name: 'SvForm',
  components: { SvBooleanField, SvFormFields, SvButton },
  props: ['identifier', 'title', 'url', 'method', 'fields', 'actions'],

  data() {
    return {
      form: {},
      ready: false,
      compact: false
    }
  },

  created() {
    this.form = new FormModel(this.$props, this.$superv, this.$set)

    if (this.$parent.form) {
      this.form.initParent(this.$parent.form)
    }
    this.form.init()

    this.ready = true
    this.$emit('form', this.form)
  },
}
</script>

<style>
.form--sub-actions {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 600ms, visibility 600ms;
  transition: opacity 600ms, visibility 600ms;
}

.form--actions:hover > .form--sub-actions {
  visibility: visible;
  opacity: 1;
}
</style>
