import Vue from 'vue'

const event = {
  bus: null,

  debug: false,

  init() {
    if (!this.bus) {
      this.bus = new Vue()
    }

    return this
  },

  emit(name, ...args) {
    this.bus.$emit(name, ...args)
    return this
  },

  fire(...args) {
    if (this.debug) {
      svdebug(args[0], 'eventfire', args[1])
    }

    return this.emit(...args)
  },

  listen(...args) {
    return this.on(...args)
  },

  on() {
    if (arguments.length === 2) {
      this.register(arguments[0], arguments[1])
    } else {
      Object.keys(arguments[0]).map(key =>
        this.register(key, arguments[0][key]),
      )
    }

    return this
  },

  register(event, handler) {
    const thing = this.bus.$on(event, handler)
  },
}

export { event }
