<script>
import SvSidebar from './SvSidebar'
import SvSiteHeader from './SvSiteHeader'
import SvSiteFooter from './SvSiteFooter'
import SvStackedRouter from '../SvStackedRouter'
import SvTabbedRouter from '../SvTabbedRouter'
import SvRouter from '../SvRouter'

export default {
  name: 'LayoutDefault',
  components: {
    SvRouter,
    SvTabbedRouter,
    SvStackedRouter,
    SvSiteFooter,
    SvSiteHeader,
    SvSidebar,
  },
  props: ['router-type'],
  data() {
    return {
      layout: this.$store.layout,
    }
  },
  computed: {
    minimized() {
      return !this.layout.sidebar.fixed && !this.layout.sidebar.hover
    },

    fullscreen() {
      return this.$store.$state.fullscreen
    },
  },
}
