<template>
  <div :class="{'full': minimized}"
       class="sv-header bg-bg-alt flex text-white justify-between items-center py-1 px-4"
  >

    <div class="sv-sidebar-top flex bg-bg-alt">
      <div class="brand">
        <svg class="inline-block fill-current text-green-700 w-8" fill="none" viewBox="0 -20 100 120"
             xmlns="http://www.w3.org/2000/svg">
          <path clip-rule="evenodd"
                d="M73.063 0L100 23.547 50.037 75 0 23.547 26.79 0h46.273zm16.014 23.547L70.332 7.195H29.52L10.996 23.547l39.04 40.48 39.041-40.48z"
                fill-rule="evenodd"/>
          <path class="fill-current text-green-500"
                d="M23.173 23.547h9.963l17.417 19.269 17.122-19.27h9.373L50.553 53.489l-27.38-29.941z"/>
        </svg>
      </div>
      <div class="buttons">
        <a @click.prevent="toggleSidebar"
           class="hover:underline font-bold  no-underline">
          <sv-icon :class="[sidebar.fixed ? 'text-gray-700' : 'text-white']" class="text-xl" name="menu"></sv-icon>
        </a>

      </div>
    </div>

    <div class="sv-toolbar flex flex-row flex-1 items-center justify-end">
      <ul class="list-reset flex">
        <slot name="top-menu"></slot>
        <li>
          <sv-modal-link :url="`sv/res/users/${user.id}/rel/profile/edit`">
            <template slot="button" slot-scope="{push}">
              <a @click.prevent="push"
                 class="flex items-center no-underline text-sm text-white group   ml-10">
                <i class="fas fa-user fill-current  mr-2 group-hover:text-primary text-gray-600"></i>
                <span>{{ user.email }}</span>
              </a>
            </template>
          </sv-modal-link>

        </li>
      </ul>
      <div class="flex items-center ml-6 pl-6 border-l border-gray-700">
        <!--      <img :src="user.avatar_url" class="rounded-full h-12 w-12" style="object-fit: cover;" v-if="user.avatar_url">-->
        <div class="flex h-12 w-12 bg-grey rounded-full"></div>
        <div class="ml-3 flex-1">
          <div class="text-white text-sm font-bold ">{{ user.first_name }} {{ user.last_name }}</div>
          <a @click.prevent="$router.home()"
             class="hover:underline  font-bold  no-underline block">
            <sv-icon class="text-white" name="home"></sv-icon>
          </a>
          <a @click.prevent="logout"
             class="hover:underline  font-bold  no-underline block">
            <sv-icon class="text-white" name="power"></sv-icon>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvIcon from '../SvIcon'

export default {
  name: 'SvSiteHeader',
  components: { SvIcon },
  data() {
    return {
      layout: this.$store.layout
    }
  },
  computed: {
    sidebar() {
      return this.layout.sidebar
    },
    minimized() {
      return !this.layout.sidebar.fixed && !this.layout.sidebar.hover
    },
    fullscreen() {
      return this.$store.$state.fullscreen
    },
    user() {
      return this.$store.state.user
    },
  },
  methods: {
    toggleSidebar() {
      this.sidebar.fixed = !this.sidebar.fixed
    },
    logout() {
      this.$events.fire('logout:request')
    },
  },
}
</script>

