<template>
  <div  class="sv-tabbed-router">
    <div  v-if="pinnedTabs.length" class="tabs-nav-container">
      <ul class="tabs-nav flex-grow">
        <li v-for="tab in pinnedTabs"
            class="tabs-nav-item"
            :class="[tab.active ? 'active' : '', tab.pinned ? 'pinned' : '']"
        >
          <a class="tabs-nav-link" @click.prevent="activateTab(tab)">
            <span class="tabs-nav-link-title">{{ tab.title }}</span>
          </a>

          <a v-show="tab.pinned"
             @click.prevent="removeTab(tab)"
             class="tabs-nav-item-close">&times;</a>
        </li>

      </ul>
      <div class="inline-flex justify-between btn-tab mr-12">
        <button @click.prevent="closeAll" class="btn-tab">
          Close All
        </button>
      </div>
    </div>
    <div class="sv-page-container">
      <div v-for="tab in tabs" v-show="tab.active" class="flex flex-grow w-full h-full" :key="tab.id">
        <div class="absolute" style="top: 1.75rem; right: 1rem;">
          <a v-show="!tab.pinned" class="opacity-50 hover:opacity-100 mx-3 bg-white py-2 px-3 rounded text-xl text-gray-600 hover:text-cyan " @click="$events.fire('pin:current')">
            <i class="zmdi  zmdi-pin"></i>
          </a>
        </div>
        <component
            :is="tab.component"
            @page-title="title => tab.title = title"
            v-bind="tab.props"
        ></component>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SvTabbedRouter',
  data() {
    return {
      tabs: [],
      current: {
        component: null,
        props: {},
      },
      loaded: null,
    }
  },

  computed: {
    pinnedTabs() {
      try {
        return this.tabs.filter(tab => tab.pinned)
      } catch (e) {
        this.reset()
      }
    },
    unPinnedTab() {
      return this.tabs.find(tab => tab.pinned === false)
    },
    activeTab() {
      return this.tabs.find(tab => tab.active === true)
    },
    firstTab() {
      return this.tabs[0]
    },
  },

  created() {
    try {
      this.$events.listen('route:changed', newRoute =>
        this.routeChanged(newRoute),
      )
      this.$events.listen('pin:current', () => this.pinCurrentTab())
      this.load()

      if (this.$route) {
        this.activateRoute(this.$route)
      } else {
        alert('you should not be seeing this from tabbed router')
      }

      // in case we fail to activate any tabs
      // activate the first from stack
      this.$nextTick(() => {
        if (this.tabs.length && !this.activeTab) {
          this.$notifyError('it is happenning!')
          this.firstTab.active = true
        }
      })
    } catch (e) {
      this.reset()
    }
  },

  methods: {
    routeChanged(newRoute) {
      this.$debug('route changed')
      this.activateRoute(newRoute)
    },
    activateRoute(route) {
      const unpinned = this.unPinnedTab
      if (this.tabs.length === 0 || !unpinned) {
        this.newTab(route)
      } else {
        this.activeTab && (this.activeTab.active = false)

        unpinned.component = unpinned.props = null
        this.$nextTick(() => {
          unpinned.component = route.component
          unpinned.props = { ...route.props }
          unpinned.active = true
        })
      }
    },
    pinCurrentTab() {
      this.activeTab.pinned = true
    },

    activateTab(tab) {
      this.activeTab && (this.activeTab.active = false)

      tab.active = true
      this.save()
    },

    /**
     *
     */
    newTab(route) {
      const tab = {
        id: this.getTabId(),
        title: '',
        props: { ...route.props },
        component: route.component,
        pinned: false,
        active: true,
      }

      tab.props.tabId = tab.id

      this.activeTab && (this.activeTab.active = false)

      this.tabs.push(tab)
      this.save()
    },

    /**
     *
     * @param tab
     */
    removeTab(tab) {
      // if it's the only tab we have,
      // unpin instead of removing
      if (this.tabs.length === 1) {
        tab.pinned = false
        return
      }
      const index = this.tabs.indexOf(tab)
      if (index > -1) {
        this.tabs.splice(index, 1)
      }
      if (this.tabs.length > 0) {
        // if active is removed, activate first from stack
        if (tab.active) {
          this.$nextTick(() => (this.firstTab.active = true))
        }
      }
      this.save()
    },
    closeAll() {
      this.activeTab.pinned = false
      // this.tabs = [this.activeTab]
      this.tabs = []
      this.save()
    },
    reset() {
      this.tabs = []
      this.save()
    },
    save() {
      localStorage.setItem(
        this.$sv.config.name + ':tabs',
        JSON.stringify(this.tabs),
      )
    },

    /**
     * Load tabs from local storage
     */
    load() {
      const tabs = localStorage.getItem(this.$sv.config.name + ':tabs')
      if (tabs) {
        this.tabs = this.loaded = JSON.parse(tabs)
      }
    },
    getTabId() {
      let min = 10000
      let max = 99999
      min = Math.ceil(min)
      max = Math.floor(max)
      return Math.floor(Math.random() * (max - min)) + min
    },
  },
}
</script>

